.block_subscribe {
  background-color: white;
  text-align: center; }

/* Sweep To Right */
.subscribe_button, ul.social li a, .top_line_nenu ul li a {
  text-align: center;
  padding: 10px 30px;
  color: #4d4d4d;
  cursor: pointer;
  background: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #4d4d4d;
  border-radius: 23px;
  color: #4d4d4d;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s; }

.top_line_nenu ul li a {
  display: block;
  padding: 0 45px;
  line-height: 58px;
  border-radius: 0;
  border: 1px solid #4d4d4d;
  text-decoration: none;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

ul.social li a {
  display: inline-block;
  padding: 0;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  font-size: 1.25rem;
  border: 1px solid #4d4d4d;
  color: #4d4d4d; }

.subscribe_button:before, ul.social li a:before,
.top_line_nenu ul li a:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: -10px;
  right: -10px;
  bottom: 0;
  border-radius: 23px;
  background: #4d4d4d;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out; }

.top_line_nenu ul li a:before {
  border-radius: 0;
  color: #ffffff; }

.subscribe_button:hover, ul.social li a:hover, .top_line_nenu ul li a:hover,
.subscribe_button:focus, ul.social li a:focus, .top_line_nenu ul li a:focus,
.subscribe_button:active, ul.social li a:active, .top_line_nenu ul li a:active {
  color: #ffffff; }

.subscribe_button:hover:before, ul.social li a:hover:before, .top_line_nenu ul li a:hover:before,
.subscribe_button:focus:before, ul.social li a:focus:before, .top_line_nenu ul li a:focus:before,
.subscribe_button:active:before, ul.social li a:active:before, .top_line_nenu ul li a:active:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1); }

ul.social {
