  padding: 4px 50px;
  line-height: 50px;
  font-size: 18px;
  text-decoration: none;
  border: 0;
  letter-spacing: 1px;
  -moz-transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.button-call {
  text-align: right;
  margin: 0;
