/* конец формы обратной связи */
/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor {
  max-width: 480px;
  position: relative;
  background-color: rgba(255, 255, 255, 0.99);
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s else;
  transition: all 0.5s else;
  overflow: hidden;
  margin: auto;
  border: 1px solid #fff;
  border-radius: 10px; }

.forms-calldecor img {
  width: 100%;
  height: auto; }

.forms-calldecor h4 {
  color: #000;
  text-align: center;
  font-size: 30px;
  margin: 30px 0 40px 0; }

.forms-calldecor input {
  width: 90%;
  color: #333333; }

.forms-calldecor .button-call {
  float: none;
  text-align: center;
  color: #fff;
  margin-bottom: 20px; }

.forms-calldecor .mfp-close {
  position: absolute;
  right: 15px;
  font-size: 30px;
  top: 15px;
  border-radius: 3px;
  -moz-transition: all .75s ease;
  -webkit-transition: all .75s ease;
  transition: all .75s ease; }

.forms-calldecor .mfp-close:hover {
  color: red;
  -moz-transform: rotate(45deg);
  /* Firefox */
  -o-transform: rotate(45deg);
  /* Opera */
  -webkit-transform: rotate(45deg);
  /* Safari y Chrome */
  filter: progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=0.7071067811865476, M12=-0.7071067811865475, M21=0.7071067811865475, M22=0.7071067811865476);
  /* IE */ }

.forms-calldecor .mfp-close-btn-in .mfp-close {
  color: #fff;
  font-size: 22.5em;
  font-weight: bold;
  border-radius: 3px; }

.forms-calldecor .success {
  color: #fff;
  background-color: #7cc15a;
  position: absolute;
  z-index: 1;
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.forms-calldecor .success.active {
  top: 0;
  opacity: 1; }

.forms-calldecor .success p {
  margin: auto;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  color: #fff;
  font-size: 18px;
  margin: auto; }

.my-mfp-zoom-in .mfp-container,
.my-mfp-zoom-in.mfp-bg {
  opacity: 0;
  -webkit-backface-visibility: hidden;
  /* ideally, transition speed should match zoom duration */
  -webkit-transition: all 1.0s linear;
  -moz-transition: all 1.0s linear;
  -o-transition: all 1.0s linear;
  transition: all 1.0s linear; }

.my-mfp-zoom-in.mfp-ready .mfp-container {
  opacity: 1; }

.my-mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8; }

.my-mfp-zoom-in.mfp-removing .mfp-container,
.my-mfp-zoom-in.mfp-removing.mfp-bg {
  opacity: 0; }

.mfp-wrap ~ * {
  -moz-filter: blur(9px);
  -webkit-filter: blur(9px);
  filter: blur(9px); }

